.body{
  width:400px;
  height:800px;
  background-color:#1F1F1F;
}
.chatNav{
  display:flex;
  justify-content: space-around;
}
.nav{
  padding:10px;
  position: sticky;
  top:0px;
  background-color: #1F1F1F;
}
.chatbox{
  position: absolute;
  bottom:0px;
  margin:20px;
  display:flex;
  justify-content: space-around;
  position:fixed;
}
.chatbox >input[type="text"]{
  padding-left:10px;
  width:240px;
  margin-right:10px;
  height:25px;
  background-color:#3F3F3F;
  color:white;
  border:grey solid;
  border-radius: 2px;
}
.chatbox >button:hover{
  cursor:pointer;
  width:100px;
  color:grey;
  background-color:yellow;
  border:white;
  border-radius: 2px;
}
.chatbox >button{
  width:100px;
  background-color:yellow;
  border:yellow;
  border-radius:2px;
}
.logo{
  width:100px;
  height:50px;
}
/* Chats */
.convo{
  margin:10px;
  color:white;
  display: flexbox;
  flex-wrap: wrap;
}
.user{
  display:flex;
  border-radius:10px;
  background-color: #3F3F3F;
  padding:10px;
  margin:10px;
}
.bot{
  display:flex;
  border-radius:10px;
  background-color:#3F3F3F;
  padding:10px;
  width:auto;
  margin:10px;
}